'use client';

import React, { createContext, useContext, useState, useEffect, useCallback } from 'react';

export interface SystemState {
  // Global system metrics
  networkStatus: 'online' | 'offline' | 'degraded';
  signalStrength: number; // 0-100
  systemLoad: number; // 0-100
  activeConnections: number;
  threatLevel: 'low' | 'medium' | 'high' | 'critical';
  
  // Active monitoring
  selectedCity: string | null;
  selectedProject: string | null;
  selectedExploit: string | null;
  activeMode: 'desktop' | 'cyberworld' | 'terminal' | 'analytics';
  
  // Live data streams
  requestCount: number;
  mitigatedCount: number;
  activeScans: number;
  terminalLogs: Array<{ timestamp: string; level: 'info' | 'warn' | 'error' | 'success'; message: string }>;
  
  // Globe state
  globeMode: 'network' | 'threats' | 'projects' | 'cyberworld';
  highlightedNodes: string[];
  activeConnectionsList: Array<{ from: string; to: string; type: string }>;
  
  // Panel states
  panels: {
    analytics: { active: boolean; data: any };
    security: { active: boolean; data: any };
    terminal: { active: boolean; data: any };
    projects: { active: boolean; data: any };
  };
}

interface SystemStateContextType {
  state: SystemState;
  updateState: (updates: Partial<SystemState>) => void;
  logTerminal: (message: string, level?: 'info' | 'warn' | 'error' | 'success') => void;
  selectCity: (city: string) => void;
  selectProject: (project: string) => void;
  setGlobeMode: (mode: SystemState['globeMode']) => void;
}

const SystemStateContext = createContext<SystemStateContextType | null>(null);

const initialState: SystemState = {
  networkStatus: 'online',
  signalStrength: 95,
  systemLoad: 23,
  activeConnections: 1247,
  threatLevel: 'medium',
  selectedCity: null,
  selectedProject: null,
  selectedExploit: null,
  activeMode: 'desktop',
  requestCount: 20020,
  mitigatedCount: 127,
  activeScans: 8,
  terminalLogs: [],
  globeMode: 'network',
  highlightedNodes: [],
  activeConnectionsList: [],
  panels: {
    analytics: { active: false, data: null },
    security: { active: false, data: null },
    terminal: { active: false, data: null },
    projects: { active: false, data: null },
  },
};

export function SystemStateProvider({ children }: { children: React.ReactNode }) {
  const [state, setState] = useState<SystemState>(initialState);

  // Continuous system updates
  useEffect(() => {
    const interval = setInterval(() => {
      setState(prev => ({
        ...prev,
        requestCount: prev.requestCount + Math.floor(Math.random() * 5),
        mitigatedCount: prev.mitigatedCount + (Math.random() > 0.9 ? 1 : 0),
        systemLoad: Math.max(10, Math.min(90, prev.systemLoad + (Math.random() - 0.5) * 2)),
        signalStrength: Math.max(80, Math.min(100, prev.signalStrength + (Math.random() - 0.5) * 1)),
      }));
    }, 2000); // Update every 2 seconds

    return () => clearInterval(interval);
  }, []);

  const logTerminal = useCallback((message: string, level: 'info' | 'warn' | 'error' | 'success' = 'info') => {
    const timestamp = new Date().toLocaleTimeString('en-US', { hour12: false });
    setState(prev => ({
      ...prev,
      terminalLogs: [
        ...prev.terminalLogs.slice(-99), // Keep last 100 logs
        { timestamp, level, message }
      ],
    }));
  }, []);

  const selectCity = useCallback((city: string) => {
    setState(prev => ({
      ...prev,
      selectedCity: city,
      globeMode: 'threats',
      highlightedNodes: [city],
    }));
    logTerminal(`[SYSTEM] City selected: ${city}`, 'info');
  }, [logTerminal]);

  const selectProject = useCallback((project: string) => {
    setState(prev => ({
      ...prev,
      selectedProject: project,
      globeMode: 'projects',
    }));
    logTerminal(`[SYSTEM] Project selected: ${project}`, 'info');
  }, [logTerminal]);

  const setGlobeMode = useCallback((mode: SystemState['globeMode']) => {
    setState(prev => ({
      ...prev,
      globeMode: mode,
    }));
    logTerminal(`[SYSTEM] Globe mode: ${mode}`, 'info');
  }, [logTerminal]);

  const updateState = useCallback((updates: Partial<SystemState>) => {
    setState(prev => ({ ...prev, ...updates }));
  }, []);

  return (
    <SystemStateContext.Provider value={{ state, updateState, logTerminal, selectCity, selectProject, setGlobeMode }}>
      {children}
    </SystemStateContext.Provider>
  );
}

export function useSystemState() {
  const context = useContext(SystemStateContext);
  if (!context) {
    throw new Error('useSystemState must be used within SystemStateProvider');
  }
  return context;
}
