'use client';

import React from 'react';

interface ErrorBoundaryState {
  hasError: boolean;
  error: Error | null;
}

export class ErrorBoundary extends React.Component<
  { children: React.ReactNode },
  ErrorBoundaryState
> {
  constructor(props: { children: React.ReactNode }) {
    super(props);
    this.state = { hasError: false, error: null };
  }

  static getDerivedStateFromError(error: Error): ErrorBoundaryState {
    return { hasError: true, error };
  }

  componentDidCatch(error: Error, errorInfo: React.ErrorInfo) {
    console.error('[ErrorBoundary] Caught error:', error, errorInfo);
  }

  render() {
    if (this.state.hasError) {
      return (
        <div
          style={{
            position: 'fixed',
            inset: 0,
            display: 'grid',
            placeItems: 'center',
            background: '#000',
            color: '#ff0000',
            fontFamily: 'monospace',
            padding: 20,
          }}
        >
          <div style={{ maxWidth: 600, textAlign: 'center' }}>
            <h1 style={{ color: '#ff0000', marginBottom: 20 }}>Application Error</h1>
            <p style={{ color: '#ff6666', marginBottom: 20 }}>
              A client-side exception has occurred. Please check the browser console for details.
            </p>
            {this.state.error && (
              <pre style={{ background: '#1a1a1a', padding: 10, overflow: 'auto', fontSize: 12 }}>
                {this.state.error.toString()}
              </pre>
            )}
            <button
              onClick={() => {
                window.location.reload();
              }}
              style={{
                marginTop: 20,
                padding: '10px 20px',
                background: 'transparent',
                border: '2px solid #ff0000',
                color: '#ff0000',
                cursor: 'pointer',
                fontFamily: 'monospace',
              }}
            >
              Reload Page
            </button>
          </div>
        </div>
      );
    }

    return this.props.children;
  }
}
